* {
  margin: 0;
  padding: 0;
}

.App {
  height: 550px;
  text-align: center;
  background: #26262e;
  position: relative;
}

.extenstion-section {
  height: 550px;
  position: relative;
}

.content-container {
  height: 550px;
  display: flex;
  flex-direction: column;
}

.logo-container {
  padding: 10px;
  text-align: left;
  color: #ffff33;
}

#logo {
  width: 115px;
}

.text-container {
  height: 100%;
  padding: 10px;
}

textarea {
  height: 100%;
  margin: auto;
  background: #26262e;
  color: #f5f5f5;
  resize: none;
  border: none;
  outline: none;
  font-size: 16px;
}

.button-container {
  padding: 10px;
}

.action-btn {
  background: #ffff33;
  width: 100%;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}
